.restaurantCard {
    height: 300px;
    min-width: 300px;
    max-width: 100%;
    border: 2px solid black;
    border-radius: 5px;
    margin: 0px 25px 25px 0px;
    display: block;
    flex-grow: 1;
    flex-basis: 0;
    overflow: auto;
    text-align: left;
}
  
.restaurantHeader {
    margin-bottom: 20px;
}
  
.restaurantCardContent {
    margin: 15px;
}
  
#restaurants {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
  
.item {
    box-shadow: 0px -1px 0 #000;
    padding-top: 10px;
}
  
.itemName {
    text-align: left;
    width: 80%;
    display: inline-block;
}
  
.itemPrice {
    text-align: right;
    width: 20%;
    display: inline-block;
}
  
.itemDescription {
    width: 100%;
    font-style: italic;
}